<template lang="html">
  <div class="scroller">
    <slot name="header"></slot>
    <slot name="scroller-body"></slot>
    <slot name="footer"></slot>
    <button @click="toTop" type="button" name="button" class="wave-effect to-top float-btn pink-l-4">
      <i class="material-icons">vertical_align_top</i>
    </button>
  </div>
</template>

<script>
let lastTop = 0
const sT = 1024
const mT = 1280
export default {
  data: function () {
    return {
      columns: [[], [], []],
      media: undefined
    }
  },
  /**
   * çŽ°åœ¨åªæ”¯æŒ1,2,3
   */
  props: {
    lCol: {
      type: Number,
      default: 3
    },
    mCol: {
      type: Number,
      default: 2
    },
    sCol: {
      type: Number,
      default: 1
    }
  },
  computed: {},
  ready: function () {
    this.scrollListener()
    this.resize()
    window.addEventListener('resize', this.resize)
  },
  attached: function () {},
  methods: {
    getDebt () {
      // åˆ—è¡¨é«˜åº¦
      let E1H = document.querySelector('.col-0-List').offsetHeight
      let E2H = document.querySelector('.col-1-List').offsetHeight
      let E3H = document.querySelector('.col-2-List').offsetHeight
      // åˆ—è¡¨ä¸‹cardæ•°é‡
      let cn1 = document.querySelectorAll('.col-0-List .card').length
      let cn2 = document.querySelectorAll('.col-1-List .card').length
      let cn3 = document.querySelectorAll('.col-2-List .card').length
      // å¡ç‰‡å¹³å‡é«˜åº¦
      let eH = (E1H + E2H + E3H) / (cn1 + cn2 + cn3)
      // å¯èŽ·å¾—ä¿¡æ¯æ¡æ•°åŸºæ•°
      let c1 = 0
      let c2 = 0
      let c3 = 0
      // è®¡ç®—å·®è·
      let s1to2 = Math.round((E1H - E2H) / eH)
      let s1to3 = Math.round((E1H - E3H) / eH)
      let s2to3 = Math.round((E2H - E3H) / eH)
      // åˆ—è¡¨1æ¯”åˆ—è¡¨2é«˜ï¼Œå¹³å‡é«˜å‡º{{s1to2}}ä¸ªcardï¼Œæ‰€ä»¥å…ˆè¡¥é½åˆ—è¡¨2ï¼Œå…¶ä»–åˆ¤æ–­åŒç†
      if (s1to2 > 0) {
        c2 += s1to2
      } else {
        c1 += -s1to2
      }
      if (s1to3 > 0) {
        c3 += s1to3
      } else {
        c1 += -s1to3
      }
      if (s2to3 > 0) {
        c3 += s2to3
      } else {
        c2 += -s2to3
      }
      return {c1, c2, c3}
    },
    loadView (data) {
      // ç¬¬ä¸€æ¬¡
      let len = data.length
      if (this.columns[0].length === 0) {
        let x2 = Math.floor(len / 2)
        let x3 = Math.floor(len / 3)
        switch (this.media) {
          case 's':
            this.columns[0].push(...data)
            this.columns[1] = []
            this.columns[2] = []
            break
          case 'm':
            this.columns[0].push(...data.slice(0, x2))
            this.columns[1].push(...data.slice(x2))
            break
          case 'l':
            this.columns[0].push(...data.slice(0, x3))
            this.columns[1].push(...data.slice(x3, x3 * 2))
            this.columns[2].push(...data.slice(x3 * 2))
        }
        this.columns = [...this.columns]
        return
      }
      let rest
      let cg
      let {c1, c2, c3} = this.getDebt()
      switch (this.media) {
        case 's':
          this.columns[0].push(...data)
          break
        case 'm':
          // è®¡ç®—å‰©ä½™ä¿¡æ¯æ¡æ•°
          rest = len - (c1 + c2)
          // å¹³å‡èƒ½å¾—åˆ°ä¿¡æ¯æ¡æ•°
          cg = Math.floor(rest / 2)
          this.columns[0].push(...data.slice(0, c1 + cg))
          this.columns[1].push(...data.slice(c1 + cg))
          break
        case 'l':
          // è®¡ç®—å‰©ä½™ä¿¡æ¯æ¡æ•°
          rest = len - (c1 + c2 + c3)
          // å¹³å‡èƒ½å¾—åˆ°ä¿¡æ¯æ¡æ•°
          cg = Math.floor(rest / 3)
          let r = rest % 3
          r === 1 ? c2++ : (r === 2 ? c3++ : c1 += r)
          c1 += cg
          c2 += c1 + cg
          this.columns[0].push(...data.slice(0, c1))
          this.columns[1].push(...data.slice(c1, c2))
          this.columns[2].push(...data.slice(c2))
      }
      this.columns = [...this.columns]
    },
    resetView2s () {
      // å°å±å¹•ä¸‹ï¼Œä¿ç•™ç¬¬ä¸€åˆ—ï¼Œå…¶ä»–åˆ—æ•°æ®æ”¾åˆ°ç¬¬ä¸€åˆ—
      this.columns[0].push(...this.columns[1], ...this.columns[2])
      this.columns[1] = []
      this.columns[2] = []
    },
    resetView2m (fromMedia) {
      // ä¸­ç­‰å±å¹•ï¼Œä¿ç•™ä¸¤åˆ—
      let {c1, c2, c3} = this.getDebt()
      // å°å±å¹•è½¬ä¸­ç­‰å±å¹•, å¶æ•°æ”¾åˆ°ç¬¬äºŒåˆ—
      if (fromMedia === 's') {
        this.columns[0].forEach((it, i) => {
          if (i % 2 !== 0) {
            this.columns[1].push(it)
            this.columns[0].splice(i, 1)
          }
        })
      } else {
        let col3 = this.columns[2]
        let rest = c3 - (c1 + c2)
        let cg = Math.floor(rest / 2)
        this.columns[0].push(...col3.slice(0, c1 + cg))
        this.columns[1].push(...col3.slice(c1 + cg))
        this.columns[2] = []
      }
      this.columns = [...this.columns]
    },
    resetView2l (fromMedia) {
      if (fromMedia === 'm') {
        this.columns[0].push(...this.columns[1])
      }
      this.columns[1] = []
      this.columns[2] = []
      this.columns[0].forEach((it, i) => {
        if (i % 3 === 1) {
          this.columns[1].push(it)
          this.columns[0].splice(i, 1)
        } else if (i % 3 === 2) {
          this.columns[2].push(it)
          this.columns[0].splice(i, 1)
        }
      })
      this.columns = [...this.columns]
    },
    scrollListener () {
      document.addEventListener('scroll', this.scroll)
    },
    scroll () {
      let top = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
      let scrollHight = document.documentElement.scrollHeight || document.body.scrollHeight
      // æ‹‰å–æ–°æ•°æ®
      if (scrollHight - top < 1000 && top - lastTop > 0 && this.media === 'l') {
        window.removeEventListener('scroll', this.scroll)
        this.$emit('pull:start')
      }
      if (scrollHight - top < 500 && top - lastTop > 0 && this.media === 'm') {
        window.removeEventListener('scroll', this.scroll)
        console.log(2)
        this.$emit('pull:start')
      }
      if (scrollHight - top < 200 && top - lastTop > 0 && this.media === 's') {
        window.removeEventListener('scroll', this.scroll)
        console.log(3)
        this.$emit('pull:start')
      }
      // åˆ¤æ–­æ»šåŠ¨æ–¹å‘
      if (top - lastTop > 0) {
        this.$dispatch('scroll:bottom', top)
      } else if (top - lastTop < 0) {
        this.$dispatch('scroll:top', top)
      }
      lastTop = top
    },
    toTop () {
      document.documentElement.scrollTop = 0
      document.body.scrollTop = 0
      window.pageYOffset = 0
      lastTop = 0
      this.$dispatch('scroll:top', 0)
    },
    resize () {
      let ww = window.innerWidth
      if (ww <= sT) {
        this.media = 's'
      } else if (ww > mT) {
        this.media = 'l'
      } else {
        this.media = 'm'
      }
    }
  },
  beforDestroy () {
    window.removeEventListener('scroll', this.scroll)
  },
  components: {},
  events: {
    'data:push' (data) {
      this.loadView(data)
      this.scrollListener()
    }
  },
  watch: {
    media (media, fromMedia) {
      switch (media) {
        case 's':
          this.resetView2s(fromMedia)
          break
        case 'm':
          this.resetView2m(fromMedia)
          break
        case 'l':
          this.resetView2l(fromMedia)
          break
      }
    }
  }
}
</script>

<style lang="css" scoped>
.to-top {
  position: fixed;
  bottom: 2rem;
  right: 1rem;
}
.to-top i {
  font-size: 1.5rem;
  width: 1.5rem;
  height: 1.5rem;
}
</style>



